// mk firework 
const canvas = document.getElementById('canvas');
const ctx = canvas.getContext('2d');
const canvasWidth = window.innerWidth;
const canvasHeight = window.innerHeight;

let fireworks = []; // 
let particles = []; // 

// #
//  loop5
let limiterTotal = 5;
let limiterTick = 0;
//  loop80
let timerTotal = 80;
let timerTick = 0;

// 
let mouseCoord = { x: 0, y: 0 };
// 
let isMousedown = false;

// canvas
canvas.width = canvasWidth;
canvas.height = canvasHeight;

// 
render();

// 
canvas.addEventListener('mousemove', function(e) {
  mouseCoord = {
    x: e.pageX - canvas.offsetLeft,
    y: e.pageY - canvas.offsetTop
  };
});

canvas.addEventListener('mousedown', function(e) {
  e.preventDefault();
  isMousedown = true;
});

canvas.addEventListener('mouseup', function(e) {
  e.preventDefault();
  isMousedown = false;
});

// 
class Particle {
  // x,y
  constructor(x, y, hue) {
    this.x = x;
    this.y = y;

    // 
    this.coords = [
      [x, y],
      [x, y],
      [x, y]
    ];

    // 
    this.angle = randomRange(0, Math.PI * 2);
    // 
    this.speed = randomRange(1, 10);

    // 
    this.friction = 0.95; //  
    this.gravity = 1; // y 

    // -20+20
    this.hue = randomRange(hue - 20, hue + 20);
    // 
    this.brightness = randomRange(50, 80);
    // 
    this.alpha = 1;
    // 
    this.alphaDecay = randomRange(0.015, 0.03);
  }

  // 
  update(index) {
    //  
    this.coords.pop();
    this.coords.unshift([this.x, this.y]);

    this.speed *= this.friction; // 

    // x,y
    // /.png
    this.x += Math.cos(this.angle) * this.speed;
    this.y += Math.sin(this.angle) * this.speed + this.gravity;

    // 
    this.alpha -= this.alphaDecay;
    //  
    if (this.alpha < this.alphaDecay) {
      particles.splice(index, 1);
    }
  }

  // line
  draw() {
    ctx.beginPath();
    // 
    const [startX, startY] = this.coords[this.coords.length - 1];
    ctx.moveTo(startX, startY);
    ctx.lineTo(this.x, this.y);
    // hsla
    ctx.strokeStyle = `hsla(${this.hue}, 100%, ${this.brightness}%, ${this.alpha}`;
    ctx.lineWidth = 3;
    ctx.lineCap = 'round';
    ctx.stroke();
  }
}

// 
class Firework {
  // sx,sy tx,ty
  constructor(sx, sy, tx, ty) {
    // 
    this.x = sx;
    this.y = sy;
    // 
    this.sx = sx;
    this.sy = sy;
    // 
    this.tx = tx;
    this.ty = ty;

    // 
    this.distanceToTarget = calcPointsDistance(sx, sy, tx, ty);
    // 
    this.distanceTraveled = 0;

    // 
    this.coords = [
      [this.x, this.y]
    ];

    // Math.atan20,0xy,x
    // /.png   
    // updatevx,vy
    this.angle = Math.atan2(ty - sy, tx - sx);
    this.speed = 2; // 2
    this.acceleration = 1.05; // 
    this.hue = randomRange(0, 360);
    this.brightness = randomRange(50, 70); // 
  }

  // 
  update(index) {
    //  
    this.coords.pop();
    this.coords.unshift([this.x, this.y]);

    this.speed *= this.acceleration; // 

    const vx = Math.cos(this.angle) * this.speed;
    const vy = Math.sin(this.angle) * this.speed;

    // 
    this.distanceTraveled = calcPointsDistance(this.sx, this.sy, this.x + vx, this.y + vy);

    //  50
    // this.x,this.y
    if (this.distanceTraveled >= this.distanceToTarget) {
      for (let i = 0; i < 75; i++) {
        particles.push(new Particle(this.tx, this.ty, this.hue));
      }

      fireworks.splice(index, 1);
    } else {
      this.x += vx;
      this.y += vy;
    }
  }

  // 
  draw() {
    ctx.beginPath();
    const [startX, startY] = this.coords[this.coords.length - 1];
    ctx.moveTo(startX, startY);
    ctx.lineTo(this.x, this.y);
    ctx.strokeStyle = `hsla(${this.hue}, 100%, ${this.brightness}%, 60%`; // 
    ctx.lineWidth = 1;
    ctx.stroke();
  }
}

function render() {
  window.requestAnimationFrame(render);

  // clearRect 
  // https://developer.mozilla.org/zh-CN/docs/Web/API/CanvasRenderingContext2D/globalCompositeOperation
  ctx.globalCompositeOperation = 'destination-out'; // 
  ctx.fillStyle = 'rgba(0, 0, 0, 0.2)';
  ctx.fillRect(0, 0, canvasWidth, canvasHeight);
  ctx.globalCompositeOperation = 'lighter';

  // 
  for (let i = 0; i < fireworks.length; i++) {
    fireworks[i].draw();
    fireworks[i].update(i);
  }
  for (let i = 0; i < particles.length; i++) {
    particles[i].draw();
    particles[i].update(i);
  }

  // 808
  if (timerTick >= timerTotal) {
    if (!isMousedown) {
      for (let i = 0; i < 8; i++) {
        fireworks.push(new Firework(canvasWidth / 2, canvasHeight, randomRange(0, canvasWidth), randomRange(0, canvasHeight / 2)));
      }

      timerTick = 0;
    }
  } else {
    timerTick++;
  }

  // 5 
  if (limiterTick >= limiterTotal) {
    if (isMousedown) {
      fireworks.push(new Firework(canvasWidth / 2, canvasHeight, mouseCoord.x, mouseCoord.y));
      limiterTick = 0; // 0
    }
  } else {
    limiterTick++;
  }
}

// 
function randomRange(min, max) {
  return Math.random() * (max - min) + min;
}

// /
//
function calcPointsDistance(sx, sy, tx, ty) {
  return Math.sqrt(Math.pow((tx - sx), 2) + Math.pow((ty - sy), 2));
}
